'use client';

import Link from 'next/link';
import { useRouter } from 'next/navigation';
import { useEffect, useState } from 'react';
import { FaArrowLeft } from 'react-icons/fa';

// Suponemos un tipo para el junior
type Junior = {
  username: string;
  password: string;
  email: string;
  rol: number;
};

export default function ModificarJuniorPage({
  params,
}: {
  params: { username: string };
}) {
  const router = useRouter();

  // Estado para formulario
  const [formData, setFormData] = useState<Junior>({
    username: '',
    password: '',
    email: '',
    rol: 3,
  });

  // Simular carga de datos por username
  useEffect(() => {
    // Reemplaza por fetch real: `/api/juniors/${params.username}`
    const mock = {
      username: params.username,
      password: 'secret123',
      email: `${params.username}@example.com`,
      rol: 3,
    };
    setFormData(mock);
  }, [params.username]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) =>
    setFormData((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    console.log('Actualizar Junior: ', formData);
    // Lógica para enviar update a API...

    // Volver
    router.push('/paginas/dashboard');
  };

  return (
    <div className="min-h-screen flex flex-col bg-black text-teal-300 font-mono px-6 py-6">
      {/* Navbar */}
      <nav className="flex justify-between items-center border-b border-teal-500/30 pb-4 mb-6">
        <h1 className="text-2xl font-bold text-yellow-300">Modificar Junior</h1>
        <Link
          href="/paginas/admin"
          className="flex items-center gap-2 text-teal-300 hover:text-orange-400 transition hover:underline"
        >
          <FaArrowLeft className="text-xl" />
          Regresar
        </Link>
      </nav>

      {/* Formulario con valores precargados */}
      <div className="max-w-md mx-auto bg-neutral-900 p-6 rounded-xl shadow-md border border-teal-500/30">
        <form onSubmit={handleSubmit} className="flex flex-col gap-4">
          <label>
            Nombre de usuario:
            <input
              type="text"
              name="username"
              value={formData.username}
              onChange={handleChange}
              required
              className="w-full mt-1 px-4 py-2 bg-black text-white border border-teal-500/30 rounded"
              disabled // no permite editar username
            />
          </label>

          <label>
            Contraseña:
            <input
              type="password"
              name="password"
              value={formData.password}
              onChange={handleChange}
              required
              className="w-full mt-1 px-4 py-2 bg-black text-white border border-teal-500/30 rounded"
            />
          </label>

          <label>
            Correo:
            <input
              type="email"
              name="email"
              value={formData.email}
              onChange={handleChange}
              required
              className="w-full mt-1 px-4 py-2 bg-black text-white border border-teal-500/30 rounded"
            />
          </label>

          <input type="hidden" name="rol" value={formData.rol} />

          <button
            type="submit"
            className="bg-yellow-600 hover:bg-yellow-700 text-white py-2 px-4 rounded transition"
          >
            Guardar Cambios
          </button>
        </form>
      </div>
    </div>
  );
}
